const ImgLogo = styled.img`
        max-width: 100px;
        max-height:250px;
        margin-bottom: 10px;
        margin-top: 10px;

    @media screen and (max-width:768px) {

    }
`;  



class NavBar extends Component {
    state = {
        isBurgerClicked : false,
    }

    handleBurgerCLick = () => {
        this.setState(prevState => ({
            isBurgerClicked: !prevState.isBurgerClicked,
        }))
    }

    render() {

    return(
        <Nav>
              <Link to={routes.home}><ImgLogo src={logo} /> </Link>
            <NavList isVisible={this.state.isBurgerClicked}>
                <NavItem>
                    <StyledNavLink to={routes.contact}>Zakupy</StyledNavLink>
                </NavItem>
                <NavItem>
                    <StyledNavLink to={routes.about}>O Nas</StyledNavLink>
                </NavItem>
                <NavItem>
                    <IconLink to={routes.contact}><i className="fas fa-shopping-cart"></i></IconLink>
                </NavItem>
                <NavItem>
                    <IconLink to={routes.about}><i class="fas fa-info"></i></IconLink>
                </NavItem>
            </NavList>
            {/* <Burger onClick={this.handleBurgerCLick} >
                <Line1 isVisible={this.state.isBurgerClicked}/>
                <Line2 isVisible={this.state.isBurgerClicked}/>
                <Line3 isVisible={this.state.isBurgerClicked}/>
            </Burger> */}
        </Nav>
 
    )
}
}



export default NavBar;