import React from 'react';
import GridTemplate from '../templates/GridTemplate';
import styled from 'styled-components'
import Header from '../components/Molecules/Header/Header';
import Footer from '../components/Organisms/Footer/Footer';
import Paragraph from '../components/Atoms/Paragraph/Paragraph';
import H from '../components/Atoms/H/H';
import Span from '../components/Atoms/Span/Span';
import NavBar from '../components/Organisms/NavBar/NavBar';
const StyledHeader = styled(Header)`
  font-size:50px;
`;
const StyledParagraph = styled(Paragraph)`
 @media screen and (max-width:768px) {
  font-size:20px;
    }
`;
const Contact = () => {


    return(
        <>
        <GridTemplate>
          <NavBar/>
            <Header>
              <H>
                Jak kupic przedmiot w 3 prostych krokrach ?
              </H>
              <Paragraph>
                1. Wyślij do nas wiadomość email, lub na FB.
              </Paragraph> 
              <Paragraph>
              2. W tytule  podaj nazwe przedmiotu.
              </Paragraph>
              <Paragraph>
              3. Poczekaj na kontakt zwrotny z danymi do przelewu.
              </Paragraph>
            </Header>
            <Footer>
              <StyledHeader>
                Masz pytania?
              </StyledHeader>
              <StyledParagraph> <Span><i className="fas fa-address-book"></i> 518-329-099 </Span> </StyledParagraph>
              <StyledParagraph> <Span><i className="fas fa-at"></i> bkproduct2018@gmail.com</Span> </StyledParagraph>
              <StyledParagraph> <Span><i className="fas fa-map-marker-alt"></i> Częstochowa 42-200</Span> </StyledParagraph>
              <StyledParagraph> <Span><i className="fas fa-calendar-times"></i> pon-pt 9:00-16:00</Span> </StyledParagraph>
              <StyledParagraph> <Span> sobota 10:00-15:00</Span> </StyledParagraph>
            </Footer>
        </GridTemplate>
              </>
    )
}

export default Contact;